* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
    box-sizing: inherit;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; 
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  height: 100%;

}

body {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0;
  font-family: $font__body;
  font-size: 16px;
  background: $bgLightBlue;
}


canvas { 
  display: block; 

} 


/* ---- particles.js container ---- */ 
#particles-js { 
    width: 100%; 
    height: 100%; 
    background-color: #000018; 
    background-position: 50% 50%; 
}


.plat-content {
  height: 100%;
  margin: 0;
  .content {
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 80px;
    width: 100%;
    .content__header {
      .button {
          display: inline-block;
          margin: 0;
          margin-left: auto;
      }
    }
    .name-input {
      input{
          font-size: 24px;
          color: $link;
          background: $bgLightBlue;
          border: none;
          border-bottom: 2px solid rgba(73,99,254,0.1);
          margin-bottom: 40px; 
          &:focus{
              color:   $link;
          }
      }
      ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
          color:   $link;
      }
      :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
         color:    $link;
         opacity:  1;
      }
      ::-moz-placeholder { /* Mozilla Firefox 19+ */
         color:    $link;
         opacity:  1;
      }
      :-ms-input-placeholder { /* Internet Explorer 10-11 */
         color:    $paragraph;
      }
      
      :focus::-webkit-input-placeholder {
          opacity: 0;
      }
      :focus:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
         opacity:  0;
      }
      :focus::-moz-placeholder { /* Mozilla Firefox 19+ */
         opacity:  0;
      }
      :focus:-ms-input-placeholder { /* Internet Explorer 10-11 */
         opacity:  0;
      }
    }
  }
}

.file-name {
  svg {
    fill: $link;
  }
}

.text-color {
  &--blue{
    color: $link;
  }
}

.text-color {
  &--blue{
    color: $link;
  }
}

.box-shadow {
  box-shadow: 0 2px 15px 0 rgba(98, 149, 255, 0.2);
}

.content-block {
  border-radius: 15px;
  padding: 30px;
  background: white;
  margin-bottom: 50px;
}


.vh-70 {
  height: 70vh;
}


.w-answer {
  background: rgba(208,2,27,0.4);
}

.color-indicator {
  height: 16px;
  width: 16px;
  border-radius: 8px;
}


.bg-color {
  &--cO {
    background-color: $cO;
  }
  &--cY{
      background-color: $cY;
  }
  &--cNG{
      background-color: $cNG;
  }
  &--cSG{
      background-color: $cSG;
  }
  &--cSLG{
      background-color: $cSLG;
  }
  &--cSB{
      background-color: $cSB;
  }
  &--cDB{
      background-color: $cDB;
  }
  &--cP{
      background-color: $cP;
  }
  &--cDP{
      background-color: $cDP;
  }
  &--cPI{
      background-color: $cPI;
  }
  &--cM{
      background-color: $cM;
  }
  &--cGR{
      background-color: $cGR;
  }
}


@import "elements/elements";
@import "typography/typography";
@import "forms/forms";
@import "page-header/header";
@import "page-footer/footer";
@import "navs/navs";



