.plat-header {
    width: 100%;
    height: 80px;
    position: fixed;
    z-index: 9;
    background-color: $vsBackground;	
    box-shadow: 0 2px 15px 0 rgba(98,149,255,0.2);
    display: flex;
    align-items: center;
    padding-right: 40px;
    .logo-wrapper {
        position: relative;
        height: 80px;
        padding-right: 20px;
        border-right: 1px solid $border-color;
        display: flex;
        align-items: center;
        transition: all .3 ease-in-out;
        &.toggle-on {
            width: 85px;
            .logo {
                display: none;
            }
        }
        .logo {
            height: 38px;
            margin-left: 30px
        }
        .navbar-toggler {
            padding: 0;
            position: absolute;
            right: 30px;
            top: 30px;
        }
    }
    .header-actions {
        position: absolute;
        right: 40px;
        display: flex;
        .user-profile {
            margin-right: 50px;
            display: flex;
            .avatar {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                overflow: hidden;
                img {
                    width: 100%;
                }
            }
            .username {
                margin-left: 10px;
                line-height: 30px;
                cursor: pointer;
                &:hover{
                    color: $link;
                    .arrowDown {
                        fill: $link;
                    }
                }
                &[aria-expanded="true"] {
                    color: $link;
                    svg {
                        transform: rotate(180deg);
                        .arrowDown {
                            fill: $link;
                        }
                    }
                }
                svg {
                    transition: all 0.3s ease-in-out;
                    margin-left: 10px;
                }
            }
        }
        .logout {
            margin-left: auto;
            line-height: 30px;
            fill: #25283D;
            &:hover{
                fill: $link;
            }
        }
    }
}