.button:not([href]):not([tabindex]).disabled:before {
	background: none;
}

.button:not([href]):not([tabindex]).disabled svg {
	fill: #4b4c4e;
}

.button:not([href]):not([tabindex]).disabled:hover {
	color: #4b4c4e;
}

.button:not([href]):not([tabindex]).disabled:hover .button__icon {
	fill: #4b4c4e;
}

.button:not([href]):not([tabindex]):hover {
	color: white;
	text-decoration: none;
}

.button:not([href]):not([tabindex]):hover .button__icon {
	fill: white;
}

.button:not([href]):not([tabindex]):hover:before {
	width: 120%;
}

.button:not([href]):not([tabindex]):active, .button:not([href]):not([tabindex]):focus {
	color: white;
	text-decoration: none;
}

.button:not([href]):not([tabindex]):active .button__icon, .button:not([href]):not([tabindex]):focus .button__icon {
	fill: white;
}

.button:not([href]):not([tabindex]):active:before, .button:not([href]):not([tabindex]):focus:before {
	width: 120%;
}

/* LINKS */
a, a:not([href]):not([tabindex]) {
	color: #4963FE;
	text-decoration: none;
}

a:hover, a:focus, a:active, a:not([href]):not([tabindex]):hover, a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):active {
	color: #324ce8;
}

a:focus, a:not([href]):not([tabindex]):focus {
	outline: none;
}

a:hover, a:active, a:not([href]):not([tabindex]):hover, a:not([href]):not([tabindex]):active {
	outline: none;
}

