.switch {
    ul {
        display: inline-block;
        position: relative;
        margin-bottom: 0;
        background: white;
        padding: 0;
        border: 2px solid $link;
        border-radius: 24px;
        line-height: 40px;
        z-index: 0;
        &::after {
            content: '';
            height: 40px;
            width: 50%;
            top: 0;
            background: $link;
            z-index: -1;
            position: absolute;
            border-radius: 24px;
        }
        li {
            display: inline-block;
            padding: 0 30px;
            width: 160px;
            text-align: center;
            &.active {
                color: white;
                font-weight: 600;
            }
        }
    }
}